@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
:root {
  --color-white: #fff;
  --color-black: #000;
  --color-orange: #ff6b08;
  --color-red: #ff001b;
  --color-background: #e5e5e5;
  --font-roboto: "Roboto", sans-serif;
  --mobile-breakpoint: 320px;
  --tablet-breakpoint: 768px;
  --desktop-breakpoint: 1280px;
}

* {
  box-sizing: border-box;
  font-family: var(--font-roboto);
  scroll-behavior: smooth;
  outline: 0;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

body, h1, h2, h3, p, ul, ol, li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.visually-hidden {
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 375px;
  margin: 0 auto;
}

@media screen and (width >= 768px) {
  .container {
    width: 768px;
    margin: 0 auto;
  }
}

@media screen and (width >= 1280px) {
  .container {
    width: 1280px;
    margin: 0 auto;
  }
}

:root {
  --info-color: #ff6b08;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.main-section {
  width: 320px;
  margin: 0 auto;
  padding: 20px 0 40px;
  font-family: Roboto, sans-serif;
}

.film-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  display: flex;
}

.film-item {
  cursor: pointer;
  width: 280px;
  height: 443px;
  list-style-type: none;
  transition: all .35s;
}

.film-item:hover {
  scale: 1.04;
}

.film-image {
  border-radius: 6px;
  width: 100%;
  height: 402px;
}

.film-details {
  margin: 5px 0 0;
}

.film-title {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 600;
  line-height: 16px;
}

.film-info {
  color: var(--info-color);
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
}

@media screen and (width >= 768px) {
  .main-section {
    width: 768px;
    padding: 60px 0;
  }

  .film-list {
    gap: 25px;
  }

  .film-item {
    width: 336px;
    height: 495px;
  }

  .film-image {
    height: 455px;
  }

  .film-details {
    margin: 5px 0 0;
  }
}

@media screen and (width >= 1280px) {
  .main-section {
    width: 1280px;
  }

  .film-list {
    justify-content: flex-start;
