  justify-content: center;
  align-items: center;
  margin: 40px 0;
  display: flex;
}

.pagination-button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 5px;
  width: 40px;
  height: 40px;
  font-weight: 600;
}

.arrows {
  color: #fff;
  background-color: #f7f7f7;
  justify-content: center;
